body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    padding:0;
    margin:0;
    box-sizing:border-box;
    outline: none;
}
.navbar{
    background-color: black;
}
.mainlogo{
    display:flex;
}
.image1{
    height:100px;
}
.image2{
    height:70px;
    margin: 15px;
}
.searchbox{
    color:white;
    background-color: transparent;
    border:0px solid white;
    margin-left:10px;
    padding:5px 10px;
    font-size: 20px;
}
nav{
    display:flex;
    width:100%;
    position:relative;
}
.rightsidebar{
    width:40%;
    position:absolute;
    right:0;
    height: 100%;
    align-items: center;
    display: flex;
}
::-webkit-input-placeholder{
    color:white;
    opacity:1;
}
::placeholder{
    color:white;
    opacity:1;
}
::ms-input-placeholder{
    color:white;
}
.enclosingsearch_box{
    border:2px solid white;
    color:white;
    height:40px;
    width:270px;
    display: flex;
    align-items: center;
}
#search{
    margin-left: 10px;
    font-size:20px;
}
.bagsvg svg{
    fill:white;
    height:40px;
    width:30px;
    margin-left: 30px;
}
.loginimgdiv{
    width:50px;
    height:50px;
    background-color: white;
    border:0px solid white;
    border-radius: 50%;
    margin-left:30px;
}
.dropdownarrow{
    margin-left: 20px;
}
.dropdownarrow svg{
    fill:white;
    width:20px;
    height:20px;
}
.menulist{
    background-color:rgb(27,27,27);
    display:flex;
    color:white;
    padding-left: 10px;
    position:relative;
}
.menulist div{
    margin-left:10px;
    margin-right:10px;
    margin-top: 8px;
    margin-bottom: 8px;
}
.crescendo,.lagrace,.forum,.members,.grouplist,.otherevents{
    display: none;
}
.google_acc_bar{
    width: -webkit-max-content;
    width: max-content;
    border:0.5px solid white;
    background-color: black;
    color:white;
    position:absolute;
    top:80px;
    right:35px;
    padding:12px 10px;
}
.google_acc_bar div{
    padding: 10px 0px;
}
.logout_linebrk{
    margin:8px 0px;
}
.opened{
    display: block !important;
}
.closed{
    display: none !important;
}
.google_acc_bar{
    display: none;
}
